<template>
  <a-form-model
    ref="form"
    :model="formData"
    layout="vertical"
    autocomplete="off"
    :rules="rules"
    class="h-father"
  >
    <g-body-form-layout>
      <!-- 搜索插槽 -->
      <template #search-card>
        <div class="search-card">
          <g-container-list :value.sync="formData.batchNo">
          </g-container-list>
        </div>
      </template>

      <!-- 展开插槽 -->
      <template #expand-card>
        <div class="expand-card">
          <g-collapse headerName="批次信息">
            <template #content>
              <a-row :gutter="24">
                <a-col :span="6">
                  <a-form-model-item label="批次编号">
                    <a-input v-model="batchInfo.batchNo" disabled />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="物料编码">
                    <a-input v-model="batchInfo.materialCode" disabled />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="物料名称">
                    <a-input v-model="batchInfo.materialName" disabled />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="当前状态">
                    <a-input v-model="batchInfo.currentStatus" disabled />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </template>
          </g-collapse>
        </div>
      </template>

      <template #card-body>
        <g-collapse headerName="报工信息">
          <template #content>
            <a-row :gutter="24">
              <a-col :span="6">
                <a-form-model-item label="报工数量" prop="quantity">
                  <a-input-number
                    v-model="formData.quantity"
                    placeholder="请输入数量"
                    :min="1"
                    style="width: 100%"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :span="6">
                <a-form-model-item label="工位" prop="workstation">
                  <a-input
                    v-model="formData.workstation"
                    placeholder="请输入工位"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :span="6">
                <a-form-model-item label="操作员" prop="operator">
                  <a-input
                    v-model="formData.operator"
                    placeholder="请输入操作员"
                  />
                </a-form-model-item>
              </a-col>
              <a-col :span="6">
                <a-form-model-item label="报工时间" prop="timestamp">
                  <a-date-picker
                    v-model="formData.timestamp"
                    show-time
                    format="YYYY-MM-DD HH:mm:ss"
                    placeholder="选择时间"
                    style="width: 100%"
                    value-format="YYYY-MM-DD HH:mm:ss"
                  />
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </template>
      <template #g-fixed-row>
        <div class="g-fixed-row">
          <g-btn size="large" text="刷新" @callback="refresh"></g-btn>
          <g-btn size="large" text="提交" type="primary" @callback="submitForm">
          </g-btn>
        </div>
      </template>
    </g-body-form-layout>
  </a-form-model>
</template>

<script>



import axios from 'axios';

export default {
  name: 'Thruput',
 
  data() {
    return {
      formData: {
        batchNo: '',
        quantity: null,
        workstation: '',
        operator: '',
        timestamp: null,
      },
      batchInfo: {
        batchNo: '',
        materialCode: '',
        materialName: '',
        currentStatus: ''
      },
      loading: false,
      rules: {
        quantity: [
          { required: true, message: '请输入报工数量', trigger: 'blur' },
        ],
        workstation: [
          { required: true, message: '请输入工位', trigger: 'blur' },
        ],
        operator: [{ required: true, message: '请输入操作员', trigger: 'blur' }],
        timestamp: [
          { required: true, message: '请选择报工时间', trigger: 'change' },
        ],
      },
    }
  },
   watch: {
    "formData.batchNo": {
      handler(newBatchNo) {
        if (newBatchNo) {
          this.fetchBatchInfo(newBatchNo);
        } else {
          // 清空批次信息
          Object.assign(this.batchInfo, {
            batchNo: '',
            materialCode: '',
            materialName: '',
            currentStatus: ''
          });
        }
      },
    },
  },
  methods: {
    async submitForm() {
      try {
        const valid = await this.$refs.form.validate();
        if (!valid) return;

        this.loading = true;

        // 模拟 API 请求
        const response = await axios.post('/api/thruput', {
          ...this.formData
        });

        if (response.data.success) {
          this.$message.success(response.data.message);
          // 刷新
          this.refresh();

        } else {
          this.$message.error(response.data.message);
        }
      } catch (error) {
        console.error('Error submitting form:', error);
        this.$message.error('报工失败');
      } finally {
        this.loading = false;
      }
    },
    async fetchBatchInfo(batchNo) {
        // 模拟根据批次号获取批次信息
        try {
            // 假设的API
            // const response = await axios.get(`/api/batch/${batchNo}`);
            // Object.assign(this.batchInfo, response.data);
            
            // 模拟数据
            setTimeout(() => {
                Object.assign(this.batchInfo, {
                    batchNo: batchNo,
                    materialCode: 'M' + Math.floor(Math.random() * 1000),
                    materialName: '模拟物料' + Math.floor(Math.random() * 100),
                    currentStatus: '生产中'
                });
            }, 500);
        } catch (error) {
            console.error("获取批次信息失败", error);
            message.error("获取批次信息失败");
        }
    },
    refresh() {
      // 重置表单和批次信息
        Object.assign(this.formData, {
            batchNo: '',
            quantity: null,
            workstation: '',
            operator: '',
            timestamp: null,
          });
        Object.assign(this.batchInfo, {
            batchNo: '',
            materialCode: '',
            materialName: '',
            currentStatus: ''
        });
    }
  }
};
</script>

<style scoped>
/* 添加样式 */
.search-card {
  margin-bottom: 16px;
}

.g-fixed-row {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}
</style>